.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-top: 20px;
}

nav {
  margin-right: 20px;
}

.nav-active {
  padding: 30px;
  width: 50%;
  color: #ff6060;
  position: relative;
  text-decoration: underline;
  font-size: 20px;
  font-weight: 500;
}

.nav-inactive {
  padding: 30px;
  width: 50%;
  color: #ff6060;
  position: relative;
  text-decoration: none;
  font-size: 20px;
  font-weight: 500;
}

.logo {
  height: 47px;
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 8px;
}

@media screen and (max-width: 767px) {
  nav {
    margin-right: 10px;
  }

  .nav-active {
    padding: 10px;
    font-size: 15px;
    text-transform: uppercase;
  }

  .nav-inactive {
    padding: 10px;
    font-size: 15px;
    text-transform: uppercase;
  }

  .logo {
    height: 35px;
    margin-left: 20px;
    margin-right: 70px;
  }
}
