.carousel {
  height: 415px;
  margin: 30px 0 30px 50px;
  position: relative;
  width: 92%;
}

.vector {
  position: absolute;
  z-index: 1;
  cursor: pointer;
  width: 40px;
}

.vector-left {
  margin-left: 25px;
  margin-top: -20px;
  top: 50%;
}

.vector-right {
  margin-top: -20px;
  margin-right: 25px;
  right: 0;
  top: 50%;
}

.carousel-img {
  border-radius: 15px;
  height: 415px;
  object-fit: cover;
  display: none;
  width: 100%;
}

.carousel-img.actif {
  display: block;
}

.carousel-count {
  display: none;
}

@media screen and (min-width: 769px) {
  .carousel-count {
    display: block;
    position: absolute;
    left: 50%;
    top: 90%;
    color: white;
    font-weight: 500;
    font-size: 18px;
  }
}

@media screen and (max-width: 767px) {
  .carousel {
    height: 260px;
    margin: 0 auto;
    bottom: -20px;
  }
  .carousel-img {
    height: 255px;
  }
  .vector {
    width: 15px;
  }
  .vector-right {
    margin-right: 5px;
  }
  .vector-left {
    margin-left: 5px;
  }
}
